.footer {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background-color: rgb(232, 232, 233);
    padding: 20px;
    box-sizing: border-box;
}
.footer .footer_container {
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 1470px;
    margin: auto;
    justify-content: space-between;
    gap: 25px;
}

.footer .footer_left,
.footer .footer_right {
    display: flex;
    flex-direction: column;
    gap: 6px;
}
